.banner {
    margin-bottom: rem(12px);
    position: relative;

    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: #F0F0F0;
    }
}


.banner__wrap {
    position: relative;
    background: url("../img/main/slider.png") center bottom / cover no-repeat;
    min-height: rem(650px);
    background-position: 70% 30%;
    color: #FFFFFF;
    position: relative;
    z-index: 1000;
}


.banner__wrap--discounts {
    background: url("../img/main/banner.png") left center/ contain no-repeat;
    background-position: left 70% center;
    min-height: rem(400px);

    @include tablets() {
        background-size: cover;
        background-position: left 0;
    }

    @include phones() {
        background-size: cover;
        background-position: 33%;
    }
}

.banner-discounts {
    margin-bottom: rem(68px);
}

.banner__title {
    font-weight: 600;
    font-size: rem(55px);
    line-height: rem(65px);
    margin-bottom: rem(31px);
    text-transform: uppercase;

    &--discounts {
        margin: 0;
    }
}

.banner__text {
    margin-bottom: rem(34px);
    display: block;
    line-height: rem(26px);

    &--discounts {
        margin-bottom: rem(3px);
        font-weight: 300;
        font-size: rem(17px);
        line-height: rem(25px);
    }
}


.banner__desc {
    position: relative;
    padding-top: rem(190px);
    width: 44%;
    padding-bottom: rem(60px);
    color: $grey;
    padding-left: rem(50px);

    &--discounts {
        padding-top: rem(138px);
        width: 45%;
        font-weight: 600;
        font-size: rem(55px);
        line-height: rem(65px);
        text-transform: uppercase;
    }

    @include tablets() {
        padding-top: rem(150px);
        width: 65%;
    }

    @include phones() {
        width: 85%;
    }
}

// .slider__list

.slider {
    position: relative;
}

.slick-track {
    display: flex;
}

.slick-list {
    overflow: hidden;
}

.slick-list .slick-track {
    display: flex;
    align-items: flex-start;
}

.slider__item {
    height: 100%;
}

.controls-banner {
    position: absolute;
    top: 50%;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 2%;
    z-index: 10000;
}

.controls-banner__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    span {
        font-weight: 300;
        font-size: rem(14px);
        line-height: rem(21px);
        text-transform: uppercase;
    }
}

.controls-banner__icon {
    width: rem(11px);
    height: rem(22px);
    fill: transparent;


    &--prev {
        transform: rotate(180deg);
    }
}