.desc-card__wishlist {
    font-size: rem(13px);
    line-height: rem(19px);
    color: $brown;
    margin-bottom: rem(3px);
    transition: all .3s ease;
    font-weight: 300;
    color: $brown;

    &:hover {
        color: $darkBrown;
    }
}

.desc-card__title{
    font-size: rem(30px);
    line-height: rem(45px);
    margin-bottom: rem(20px);
    font-weight: 600;
    text-transform: uppercase;
    color: #333333;
}

.rating__review {
    font-size: rem(13px);
    line-height: rem(19px);
    color: #828282;
    margin-left: rem(5px);
}

.desc-card__rating {
    display: flex;
    align-items: center;
}

.desc-card__price {
    margin-top: rem(17px);
    margin-bottom: rem(21px);
}

.desc-card__price-discount {
    font-size: rem(20px);
    font-weight: 300;
    line-height: rem(30px);
    text-decoration-line: line-through;
    color: #828282;
} 

.desc-card__price-actual {
    font-size: rem(30px);
    line-height: rem(45px);
    font-weight: 600;
    color: $grey;
}

.desc-card__text {
    line-height: rem(22px);
    margin-bottom: rem(26px);
    display: block;
    font-weight: 300;
    font-size: rem(15px);
    color: #828282;
}