.header__contacts {
    display: flex;
    justify-content: space-between;
    padding: rem(8px) 0;
    border-bottom: rem(1px) solid rgba(0, 0, 0, 0.15);

    @include tablets() {
        display: none;
    }
}

.address {
    font-size: rem(17px);
    display: flex;

    &__item {
        margin-right: rem(33px);

        &:last-child {
            margin-right: 0;
        }
    }
}

.address__link {
    font-weight: 300;
    line-height: rem(19px);
    color: #828282;
    font-size: rem(13px);

    span {
        color: #000;
        transition: all .2s ease;
    }
}

.address__link:hover span {
    text-decoration: underline;
}

.socials {
    display: flex;
    align-items: center;

    &__item {
        margin-right: rem(15px);

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        font-weight: 300;
        font-size: rem(13px);
        line-height: rem(19px);

        &:hover {
            text-decoration: underline;
        }

    }

    &__icon {
        width: 100%;
        height: 100%;
        fill: rgba(0, 0, 0, 0.4);
    }

}