.footer__content {
    min-height: rem(422px);
    background-color: $brown;
    color: #fff;
}

.container-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__title {
    margin-top: rem(67px);
    font-size: rem(30px);
    line-height: rem(45px);
    margin-bottom: rem(7px);
    font-weight: 600;
    text-transform: uppercase;
    color: #FFFFFF;
}

.footer__subtitle {
    display: block;
    line-height: rem(30px);
    color: rgba(255, 255, 255, 0.6);
    margin-bottom: rem(30px);
    text-align: center;
    font-weight: 300;
    font-size: rem(17px);
}

.form {
    position: relative;
    margin-bottom: rem(40px);

    @include tablets() {
        width: 85%;
    }
}

.form__email {
    width: rem(611px);
    height: rem(50px);
    border: 1px solid transparent;

    @include tablets() {
        width: 100%;
    }
}

.input__error {
    border: rem(2px) solid #ff0000;
}

.copyright {
    display: block;
    margin-top: rem(20px);
    font-size: rem(13px);
    line-height: rem(30px);
    margin-bottom: rem(20px);
    font-weight: 300;
    color: #828282;
}

.navigation--footer {
    font-weight: 300;
    line-height: rem(24px);
    text-transform: uppercase;
    color: #FFFFFF;
}