.catalog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: rem(40px);

    @include tablets() {
        align-items: flex-end;
    }
}

.catalog-header__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include tablets() {
        flex-direction: column;
        align-items: flex-start;
    }
}

.catalog-header__title {
    font-weight: 500;
    font-size: rem(32px);
    line-height: rem(37px);
    margin: 0;

    @include tablets() {
        margin-bottom: rem(16px);
    }
}

.sorting-catalog {
    position: relative;
}

.catalog-header__filter {
    display: none;

    @include tablets() {
        display: block;
    }
}

.catalog-header__link {
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        transition: all .3s ease;
        fill: $brown;
        color: $brown;
    }

    span {
        font-weight: 300;
        white-space: nowrap;
        margin-right: rem(6px);
        line-height: rem(24px);
        text-transform: uppercase;
        color: $brown;
    }
}

.catalog-header__icon {
    width: rem(18px);
    height: rem(18px);
    fill: $brown;
}