.section__title {
    font-size: rem(30px);
    line-height: rem(45px);
    margin-bottom: rem(19px);
    text-align: center;
    position: relative;
    font-weight: 600;
    text-transform: uppercase;
    color: $brown;
    width: 100%;

    &--styleguide {
        text-align: initial;
        color: #fff
    }

    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 50%;
        border-bottom: 1px solid #828282;
        opacity: 0.3;

        @include phones() {
            display: none;
        }
    }

    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: 50%;
        height: 50%;
        border-bottom: 1px solid #828282;
        opacity: 0.3;

        @include phones() {
            display: none;
        }
    }

    span {
        position: relative;
        background-color: #fff;
        z-index: 1000;
        padding: 0 rem(40px);

        @include phones() {
            padding: 0;
        }
    }
}

.section__title-content {
    position: relative;
}
