.container-error {
    position: relative;
    height: 100vh;
}

.error {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.error__title {
    font-weight: 100;
    font-size: rem(200px);
    line-height: rem(250px);
    color: #828282;
    margin: 0;

}

.error__text {
    text-align: center;

    span {
        display: block;
        white-space: nowrap;
        font-weight: 300;
        font-size: rem(17px);
        line-height: rem(25px);
        color: #828282;
    }
}

.error__subtitle {
    font-weight: 600;
    font-size: rem(30px);
    line-height: rem(45px);
    text-transform: uppercase;
    color: $grey;
    margin-bottom: rem(9px);
}