.btn {
    padding: rem(15px) rem(80px);
    background-color: $grey;
    border: none;
    color: #fff;
    font-weight: 500;
    font-size: rem(18px);
    line-height: rem(23px);
    cursor: pointer;
    font-weight: 300;
    text-transform: uppercase;
    transition: background-color .4s ease;

    &:hover {
        background-color: #fff;
        color: #000;
    }

    &--white {
        color: $grey;
        background-color: #fff;

        &:hover {
            color: #fff;
            background-color: $grey;
        }
    }
}

.button--product {
    padding: rem(10px) rem(50px) !important;
}

.btn--sorting {
    padding: rem(10px) rem(40px);
}

.form__btn {
    width: rem(50px);
    height: rem(50px);
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border: none;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    border: 2px solid transparent;
    border-left: 1px solid #DADADA;

    &:hover {
        transition: all .2s ease;
        background-color: $darkBrown;
    }

    &-icon {
        width: rem(22px);
        height: rem(22px);
        fill: #fff;
    }
}