.breadcrumb {
    min-height: rem(55px);
    background: #F2F2F0;
    display: flex;
    align-items: center;
    margin-bottom: rem(33px);
}

.breadcrumb__list {
    font-size: rem(13px);
    line-height: rem(19px);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-weight: 300;
    color: #828282;
}

.breadcrumb__item {
    padding-left: rem(10px);
    padding-right: rem(10px);
    position: relative;

    &:hover {
        transition: all .3s ease;
        text-decoration: underline;

        &::before {
            color: #828282;
        }
    }

    &::before {
        display: block;
        content: "/";
        position: absolute;
        top: 0;
        right: 0;
    }

    &:last-child::before {
        display: none;
    }
}