.styleguide {
    margin-bottom: rem(67px);
}

.styleguide__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include tablets() {
        flex-wrap: wrap;
    }

    @include phones() {
        flex-direction: column;
    }
}

.styleguide__item {
    display: flex;
    position: relative;
    align-items: center;
    background: #EFEFEF;
    flex: 1;
    position: relative;
    min-height: rem(240px);
    margin-right: rem(25px);

    &--1 {
        background: #F3F3F3;
    }

    &--3 {
        background: #F8F4F5;
    }

    &:last-child {
        margin-right: 0;
    }

    @include tablets() {
        margin-right: 0;
        flex: 0 0 100%;
        margin-bottom: rem(10px);
    }

    @include phones() {
        width: 100%;
        margin-right: 0;
        margin-bottom: rem(20px);

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.styleguide__img {
    object-fit: contain;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
}

.styleguide__desc {
    position: relative;
    z-index: 10000;
    padding-left: rem(50px);

    @include tablets() {
        width: 100%;
    }

}

.styleguide__title {
    font-weight: 600;
    font-size: rem(30px);
    line-height: rem(45px);
    text-transform: uppercase;
    color: $brown;
}

.details {
    font-weight: 300;
    font-size: rem(16px);
    line-height: rem(24px);
    text-decoration-line: underline;
    text-transform: uppercase;
    color: $brown;

    &:hover {
        transition: color .2s ease;
        text-decoration: none;
        color: #000;
    }
}

.slick-list {
    width: 100%;
}

.product__list {
    width: 100%;
}

.product__img {
    width: 100%;
    height: 100%;
}